import { CqsItem, CqsSearchQuery, CqsStory } from "@rp/common/api-types"
import { Watchlist } from "@rp/common/api-types"
import { ShowMoreList } from "@rp/react/web/common/composite-components"
import { RpJsonAnnotationStyles } from "@rp/react/web/rpjson"
import { HighlightItem } from "@rp/react/web/rpjson"
import { MentionHeader } from "./MentionHeader"
import { MentionsList } from "./Mentions.styles"
import { MentionsCard } from "./MentionsCard"

type Props = {
  rowData: CqsItem
  cluster: Array<CqsStory>
  onClose: () => void
  onExclude: (item: CqsItem) => void
  onChange: (item: CqsItem) => void
  onAddToWatchlist?: (watchlist: Watchlist) => void
  query?: CqsSearchQuery
  queryId: string
  highlights: Array<HighlightItem>
  isFeedbackMode: boolean
}
const MentionsClusterView = ({
  rowData,
  cluster,
  onChange,
  onClose,
  onExclude,
  onAddToWatchlist,
  query,
  queryId,
  highlights,
  isFeedbackMode,
}: Props) => {
  return (
    <div>
      <MentionHeader
        backButtonTitle={"Back to Mentions"}
        title={`${cluster.length} Related ${cluster.length === 1 ? "Document" : "Documents"}`}
        onClose={onClose}
        item={rowData}
        onChange={onChange}
        onExclude={onExclude}
        onAddToWatchlist={onAddToWatchlist}
      />
      <RpJsonAnnotationStyles highlights={highlights}>
        <MentionsList>
          <ShowMoreList<CqsStory>
            renderItem={(item, index) => {
              return (
                <MentionsCard
                  isFeedbackMode={isFeedbackMode}
                  key={item.id}
                  story={item}
                  query={query}
                  index={index}
                  queryId={queryId}
                />
              )
            }}
            items={cluster}
          />
        </MentionsList>
      </RpJsonAnnotationStyles>
    </div>
  )
}

export { MentionsClusterView }
